import React, { useState } from "react";
import styled from "styled-components";
import { AiOutlineFile, AiOutlineFolder } from "react-icons/ai";
import { DiJavascript1, DiCss3Full, DiHtml5, DiReact } from "react-icons/di";
import "./styles.css";
import { da } from "./data";
const FILE_ICONS = {
  js: <DiJavascript1 />,
  css: <DiCss3Full />,
  html: <DiHtml5 />,
  jsx: <DiReact />
};

const StyledTree = styled.div`
  line-height: 1.5;
`;
const StyledFile = styled.div`
  padding-left: 20px;
  display: flex;
  align-items: center;
  span {
    margin-left: 5px;
  }
`;
const StyledFolder = styled.div`
  padding-left: 20px;

  .folder--label {
    display: flex;
    align-items: center;
    span {
      margin-left: 5px;
    }
  }
`;
const Collapsible = styled.div`
  height: ${(p) => (p.isOpen ? "0" : "auto")};
  overflow: hidden;
`;

const Child = ({ name }) => {
  let ext = name && name.split(".")[1];

  return (
    <StyledFile>
      {/* render the extension or fallback to generic file icon  */}
      <span>{FILE_ICONS[ext] || <AiOutlineFile />}</span>
      {FILE_ICONS[ext] || <AiOutlineFile />}
      <span>{name}</span>
    </StyledFile>
  );
};

const Parent = ({ name, children }) => {
  const [isOpen, setIsOpen] = useState(false);

  const handleToggle = (e) => {
    e.preventDefault();
    setIsOpen(!isOpen);
  };

  return (
    <StyledFolder>
      <div className="folder--label" onClick={handleToggle}>
        <span>
          {" "}
          <AiOutlineFolder />
        </span>
        <AiOutlineFolder />
        <span>{name}</span>
      </div>
      <Collapsible isOpen={isOpen}>{children}</Collapsible>
    </StyledFolder>
  );
};

const Tree = ({ children }) => {
  return <StyledTree>{children}</StyledTree>;
};

Tree.Parent = Parent;
Tree.Child = Child;

export default function App() {
  const TreeView = (data) => {
    return (
      <Tree>
        {data.map((i) => (
          <ChildTree childData={i} parent={true} />
        ))}
      </Tree>
    );
  };

  const ChildTree = ({ childData, parent }) => {
    if (parent) {
      return (
        <>
          <Tree.Parent name={childData.name}>
            {childData.children &&
              childData.children.map((i) =>
                i.children ? (
                  <ChildTree childData={i} parent={true} />
                ) : (
                  <ChildTree childData={i} parent={false} />
                )
              )}
          </Tree.Parent>
        </>
      );
    } else {
      return (
        <>
          <Tree.Child name={childData.name} />
          {childData.children &&
            childData.children.map((i) =>
              i.children ? (
                <ChildTree childData={i} parent={true} />
              ) : (
                <ChildTree childData={i} parent={false} />
              )
            )}
        </>
      );
    }
  };

  return <div className="App">{TreeView(da)}</div>;
}
